.cursor {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  contain: layout style size;
  pointer-events: none;
  &.media-blend {
    z-index: 100;
    mix-blend-mode: exclusion;
    .cursor-media {
      filter: invert(1);
    }
  }

  &:before {
    content: "";
    position: absolute;
    top: -24px;
    left: -24px;
    display: block;
    background: black;
    width: 48px;
    height: 48px;
    transform: scale(.2);
    border-radius: 50%;
  }

  .cursor-media {
    display: block;
    height:350px;
    width:350px;
    overflow: hidden;
    border-radius: 100%;
    transform: scale(0);
    margin: -175px 0 0 -175px;
    position: relative;

    video {
      height: 350px;
      left: 50%;
      transform: translateX(-50%);
      position: absolute;
    }
  }
}